<template>
  <div class="yhddoutbox">
    <div class="dddb_right_p1">
      <p1echarts />
    </div>
    <div class="dddb_right_p2">
      <p2echarts />
    </div>
    <div class="dddb_right_fontoutbox">
      <div class="dddb_right_fontoutbox_left">
        <div class="dddb_right_fontoutbox_left_box">
          <div
            v-for="(item,index) in Number(opdisPumplength)"
            :key="index"
            :style="{height:(344/Number(opdisPumplength))+'px'}"
            class="itembox"
          >{{index+1}}#泵</div>
        </div>
      </div>
      <div class="dddb_right_fontoutbox_right">
        <div class="echartsdw">(MPa)</div>
      </div>
    </div>
  </div>
</template>
<script>
import Bus from "@/bus.js";
import p2echarts from "./echartsbox/p2echarts";
import p1echarts from "./echartsbox/p1echarts";
export default {
  name: "yhddechartsbox",
  data(){
    return{
      opdisPumplength:""
    }
  },
  components: {
    p2echarts,
    p1echarts
  },
  mounted() {
    var _this=this
    Bus.$on("toshuichangddcompare",e=>{
      var OptimalMaxData=e.OptimalMaxData
      var OptimalDispatchData=e.OptimalDispatchData
      this.opdisPumplength=e.OptimalDispatchData.PumpCurveData.length
    })
  },
};
</script>
<style lang="scss" scoped>
.yhddoutbox {
  /*  width: 86vw;
      height: 393px;
      background-color: aqua; */
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  width: calc(100vw - 40px - 12vw);
  height: 394px;
  position: relative;
  /* background-color: rgb(35, 16, 204); */
  .dddb_right_p1 {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    width: calc(100vw - 40px - 12vw);
    height: 394px;
    position: absolute;
    /*  background-color: rgb(204, 16, 173); */
    pointer-events: auto;
  }
  .dddb_right_p2 {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    /*  width: calc(100vw - 40px - 12vw); */
    width: calc(100vw - 40px - 12vw);
    height: 394px;
    position: absolute;
    /*   background-color: rgb(16, 144, 204); */
    pointer-events: auto;
  }
  .dddb_right_fontoutbox {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-end;
    align-items: flex-end;
    /*  width: calc(100vw - 40px - 12vw); */
    width: calc(100vw - 40px - 12vw);
    height: 364px;
    position: absolute;
    /*   background-color: rgb(16, 144, 204); */
    pointer-events: none;
    .dddb_right_fontoutbox_left {
      display: flex;
      flex-flow: row nowrap;
      justify-content: flex-end;
      align-items: flex-end;
      width: 81.2vw;
      height: 344px;
      /*   border-left: 1px solid #6e7b8b;
        border-right: 1px solid #6e7b8b;
        border-top: 1px solid #cdcfd2;
        border-bottom: 1px solid #cdcfd2; */
      pointer-events: none;
      .dddb_right_fontoutbox_left_box {
        display: flex;
        flex-flow: column nowrap;
        justify-content: flex-end;
        align-items: center;
        width: 81.2vw;
        height: 324px;
        .itembox {
          display: flex;
          flex-flow: row nowrap;
          justify-content: flex-end;
          align-items: center;
          width: 81.2vw;
          color: #ced3d9;
          font: normal 10px "微软雅黑";
          margin-right: 20px;
          /* border-bottom: 1px solid #cdcfd2; */
        }
      }
    }
    .dddb_right_fontoutbox_right {
      display: flex;
      flex-flow: row nowrap;
      justify-content: flex-start;
      align-items: flex-start;
      pointer-events: none;
      width: 35px;
      height: 334px;
      position: relative;
      .echartsdw {
        color: #999;
        font: normal 10px "微软雅黑";
        position: absolute;
        top: -32px;
        right: 5px;
      }
    }
  }
}
</style>


