<template>
  <div class="outbox">
    <div :class="className" :style="{height:height,width:width}" />
  </div>
</template>
<script>
import { debounce } from "@/utils";
import Bus from "@/bus.js";
export default {
  name: "p2echarts",
  props: {
    className: {
      type: String,
      default: "chart"
    },
    width: {
      type: String,
      default: "100%"
    },
    height: {
      type: String,
      default: "260px"
    },
    autoResize: {
      type: Boolean,
      default: true
    }
    /*  chartData: {
      type: Object,
      required: true
    } */
  },
  data() {
    return {
      chart: null,
      sidebarElm: null,
      chartData: {}
    };
  },
  watch: {
    chartData: {
      deep: true,
      handler(val) {
        this.setOptions(val);
      }
    }
  },
  mounted() {

 Bus.$on("toshuichangddcompare", e => {
       var ActualMaxData=(Number(e.ActualMaxData)*2).toFixed(2)
      var ActualDispatchData=e.ActualDispatchData
  /*  */
      var time = [];
      var flow = [];
      var presure = [];
      var totalbeng = [];

      for (var i = 0; i < ActualDispatchData.FlowData.length; i++) {
        time.push(ActualDispatchData.FlowData[i].Time);
        flow.push(ActualDispatchData.FlowData[i].Data);
       
      }
      for (var i = 0; i < ActualDispatchData.PressureData.length; i++) {
        presure.push(ActualDispatchData.PressureData[i].Data);
      }
      for (var i = 0; i < ActualDispatchData.PumpCurveData.length; i++) {
         var bengname =ActualDispatchData.PumpCurveData[i].PumpNo+ "#泵";
          var prebeng = [];
         for(var j=0;j<ActualDispatchData.PumpCurveData[i].PumpData.length;j++){
            prebeng.push(ActualDispatchData.PumpCurveData[i].PumpData[j].Data)
         }
        var obj = {
          bengname,
          prebeng
        };
        totalbeng.push(obj);
      }


      {
        var bengname = "总瞬时流量";
        var prebeng = flow;
        var obj1 = {
          bengname,
          prebeng
        };
      }
      {
        var bengname = "出厂压力";
        var prebeng = presure;
        var obj2 = {
          bengname,
          prebeng
        };
      }

      totalbeng.push(obj1, obj2);
   console.log("%ctotalbeng", "color:red", totalbeng);
    var series1 = [];
    for (var i = 0; i < totalbeng.length; i++) {
      var name = totalbeng[i].bengname;
      var type = "line";
      var symbol = "none";
      if (totalbeng[i].bengname == "总瞬时流量") {
        var color = "#0070ba";
        var zlevel = -13;
         var yAxisIndex=0
      } else if (totalbeng[i].bengname == "出厂压力") {
        var color = "#d54100";
        var zlevel = 0;
         var yAxisIndex=1
      } else {
        var color = "rgba(255,255,255,0)";
       // var color = "black";
        var zlevel = 13;
         var yAxisIndex=1
      }
      var normal = { color };
      var itemStyle = { normal };
      var lineStyle = { normal };
      var data = totalbeng[i].prebeng;
      var obj = { name, type, symbol, itemStyle, zlevel, data, lineStyle ,yAxisIndex};
      series1.push(obj);
    }

    console.log("%cseries1", "color:red", series1);
    var a = {
      series1,
      time,
      ActualMaxData
    };
    this.chartData = a;
    this.$nextTick(this.initChart());
    });

   
    if (this.autoResize) {
      this.__resizeHandler = debounce(() => {
        if (this.chart) {
          this.chart.resize();
        }
      }, 100);
      window.addEventListener("resize", this.__resizeHandler);
    }

    // 监听侧边栏的变化
    this.sidebarElm = document.getElementsByClassName("sidebar-container")[0];
    this.sidebarElm &&
      this.sidebarElm.addEventListener(
        "transitionend",
        this.sidebarResizeHandler
      );
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    if (this.autoResize) {
      window.removeEventListener("resize", this.__resizeHandler);
    }

    this.sidebarElm &&
      this.sidebarElm.removeEventListener(
        "transitionend",
        this.sidebarResizeHandler
      );

    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    sidebarResizeHandler(e) {
      if (e.propertyName === "width") {
        this.__resizeHandler();
      }
    },
    setOptions(chartData) {
      this.chart.setOption({
        grid: {
          bottom: 20,
          top: 40,
          right: 85,
          left: 55,
          show: true,
          borderColor: "#dfdfe1",
          borderWidth: 1
        },
        title: {
          text: "(m³/h)",
          left: 25,
          top: 10,
          textStyle: {
            color: "#999",
            fontWeight: "normal",
            fontSize: 12
          }
        },
        legend: {
          show: true,
          orient: "horizontal",
          right: 80,
          width: 600,
          top: 10,
          itemWidth: 15,
          itemGap: 20,
          /* data: ["流量", "压力"] */
          data: ["总瞬时流量", "出厂压力"]
         /* formatter: function(params) {
           console.log("总瞬时流量",params)
          }, */
        },
        tooltip: {
          trigger: "axis",
          padding: [0, 10],
          confine: true,
          /*  formatter: function(params) {
           
          }, */
          textStyle: {
            align: "left"
          }
        },
        xAxis: [
          {
            data: chartData.time,
            axisLabel: {
              inside: false,
              textStyle: {
                color: "#fff"
              }
            },
            axisTick: {
              show: false
            },
            axisLine: {
              show: false
            },
            z: 10
          }
        ],
        yAxis: [
          {
            /*  splitNumber: 6, */
            /*  min:0.09, */
             name: "总瞬时流量",
            /*  type: "value", */
             nameTextStyle:{
               color:"rgba(255,255,255,0)"
             },
            splitLine: {
              show: false,
              lineStyle: {
                color: "#dfdfdf",
                width: 1,
                type: "dashed"
              }
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: "#6e7b8b",
                width: 1
              }
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              textStyle: {
                color: "#999"
              }
            }
          },
          {
           /*  type: "value", */
            scale: true,
            max: chartData.ActualMaxData,
            min: 0,
            boundaryGap: [0.2, 0.2],
              name: "出厂压力",
               nameTextStyle:{
               color:"rgba(255,255,255,0)"
             },
            position: "right",
            offset: 45,
            splitLine: {
              show: false,
              lineStyle: {
                color: "#dfdfdf",
                width: 1,
                type: "dashed"
              }
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: "#6e7b8b",
                width: 1
              }
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              textStyle: {
                color: "#999"
              }
            }
          }
        ],
        series: chartData.series1
       
      });
    },
    initChart() {
      this.chart = this.$echarts.init(this.$el, "macarons");
      this.chart.clear();
      this.setOptions(this.chartData);
    }
  }
};
</script>
<style lang="scss" scoped>
.outbox {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  width: calc(100vw - 40px - 12vw);
  height: 394px;
  /*   background-color: aqua */
  /*  border-top:1px solid #dfdfe1 */
}
</style>





