  <div class="outbox">
    <div class="outbox_title">
      <div class="outbox_title_leftoutbox">调度曲线</div>
      <div class="outbox_title_rightoutbox">
        <div class="outbox_title_rightoutbox_selectbox1">
          <div class="outbox_title_rightoutbox_selectbox1_left">时间:</div>
          <div class="outbox_title_rightoutbox_selectbox1_right">
            <el-date-picker
              v-model="form.birthdayName"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              @change="selectTime"
              type="date"
              placeholder="选择日期"
            ></el-date-picker>
          </div>
        </div>
      </div>
    </div>
    <div class="outbox_kdc">
      <div class="outbox_kdc_inbox">
        <div class="outbox_kdc_inbox_num">
          <div
            v-for="(item1,index1) in 24"
            :key="index1"
            class="item1_box"
            :style="{width:(1586/24)+'px'}"
          >{{item1-1==24?"":item1-1}}</div>
        </div>
        <div class="outbox_kdc_inbox_line">
          <div
            v-for="(item2,index2) in 96"
            :key="index2"
            class="item2_box"
            :class="(index2%4)==0?'longlinestyle':''"
          ></div>
        </div>
      </div>
    </div>
    <div class="outbox_echartsbox1">
      <div class="outbox_echartsbox1_left">优化调度曲线</div>
      <div class="outbox_echartsbox1_right">
        <echarts1 />
      </div>
    </div>
    <div class="outbox_echartsbox2">
      <div class="outbox_echartsbox2_left">实际调度曲线</div>
      <div class="outbox_echartsbox2_right">
        <echarts2 />
      </div>
    </div>
  </div>
</template>
<script>
//import aa from './ddcomparettable'
import { Loading } from "element-ui";
import Bus from "@/bus.js";
import urlClass from "@/components/js/UrlClass";
import echarts1 from "./echartsbox/echarts1";
import echarts2 from "./echartsbox/echarts2";
export default {
  name: "index",
  components: {
    echarts1,
    echarts2
  },
  data() {
    return {
      form: {
        birthdayName: ""
      }
    };
  },
  mounted() {
    var date = new Date();
    this.form.birthdayName = this.$moment()
      .subtract(1, "days")
      .format("YYYY-MM-DD");
    this.$nextTick(this.DispatchContrastCurveData(this.form.birthdayName));
    this.$nextTick(this.DispatchContrastTableData(this.form.birthdayName));
  },
  methods: {
    /* 1.日期选择 */
    selectTime(val) {
      this.form.birthdayName = val;
      //console.log(val);
       this.$nextTick(this.DispatchContrastCurveData(val));
    this.$nextTick(this.DispatchContrastTableData(val));
    },
    /* 2.	获取调度对比曲线 DispatchContrastCurveData */
    DispatchContrastCurveData(e) {
      var _this = this;
      var Date = e;
      //console.log("3.		获取调度对比曲线 参数Date",Date);
       _this.$axios
        .post(
          urlClass.axiosUrlRC + "DispatchContrastCurveData",
          //"https://www.easy-mock.com/mock/5d50cb82fde7c63539e6f0ad/DispatchContrastCurveData/DispatchContrastCurveData",
          JSON.stringify({ Date }),
          {
            headers: { "Content-Type": "application/json;" }
          }
        )
        .then(res => {
          //console.log("2.	获取调度对比曲线 DispatchContrastCurveData ", res);
           Bus.$emit("toddflowecharts", res.data);
        })
        .catch(error => {});
    },
    /* 3.获取调度数据列表 DispatchContrastTableData */
    DispatchContrastTableData(e) {
      var _this = this;
      var Date = e;
          //console.log("3.	获取调度数据列表 参数Date",Date);
        _this.$axios
        .post(
          urlClass.axiosUrlRC + "DispatchContrastTableData",
           //"https://www.easy-mock.com/mock/5d0ae195b491487ec1e43e92/rcddcomparetable/rcddcomparetable",
          JSON.stringify({ Date }),
          {
            headers: { "Content-Type": "application/json;" }
          }
        )
        .then(res => {
          //console.log("3.获取调度数据列表 DispatchContrastTableData ", res);
           Bus.$emit("toddfn", res.data.ContrastTableData);
          //Bus.$emit("toddfn", aa.a.ContrastTableData);
          var ddtablelelemgth = res.data.ContrastTableData.length;
          Bus.$emit("torcddcompareindex", ddtablelelemgth);
          Loading.service().close();
        })
        .catch(error => {});

    }
  }
};
</script>
