<template>
  <div :class="className" :style="{height:height,width:width}"/>
</template>

<script>
import Bus from "@/bus.js";
import { debounce } from "@/utils";
export default {
  name: "echarts1",
  props: {
    className: {
      type: String,
      default: "chart"
    },
    width: {
      type: String,
      default: "100%"
    },
    height: {
      type: String,
      default: "260px"
    },
    autoResize: {
      type: Boolean,
      default: true
    }
    /*  chartData: {
      type: Object,
      required: true
    } */
  },
  data() {
    return {
      chart: null,
      sidebarElm: null,
      chartData: {}
    };
  },
  watch: {
    chartData: {
      deep: true,
      handler(val) {
        this.setOptions(val);
      }
    }
  },
  mounted() {
/*  */
Bus.$on("toddflowecharts",e=>{
  var OptimalDispatchCurve=e.OptimalDispatchCurve
  var OptimalMaxData=Number(e.OptimalMaxData)*1.1
   var time = [];
    var waterdata = [];
    var warningdata = [];
    for (var i = 0; i < OptimalDispatchCurve.length; i++) {
      var predata =Number(OptimalDispatchCurve[i].Data).toFixed(2);
      var warnpre = Number(OptimalDispatchCurve[i].IsDispatch) == 1 ? OptimalMaxData : "";
      time.push(OptimalDispatchCurve[i].Time);
      waterdata.push(predata);
      warningdata.push(warnpre);
    }
    var a = {
      time,
      waterdata,
      warningdata
    };
    this.chartData = a;
    this.$nextTick(this.initChart());
})
/*  */
   
    if (this.autoResize) {
      this.__resizeHandler = debounce(() => {
        if (this.chart) {
          this.chart.resize();
        }
      }, 100);
      window.addEventListener("resize", this.__resizeHandler);
    }

    // 监听侧边栏的变化
    this.sidebarElm = document.getElementsByClassName("sidebar-container")[0];
    this.sidebarElm &&
      this.sidebarElm.addEventListener(
        "transitionend",
        this.sidebarResizeHandler
      );
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    if (this.autoResize) {
      window.removeEventListener("resize", this.__resizeHandler);
    }

    this.sidebarElm &&
      this.sidebarElm.removeEventListener(
        "transitionend",
        this.sidebarResizeHandler
      );

    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    sidebarResizeHandler(e) {
      if (e.propertyName === "width") {
        this.__resizeHandler();
      }
    },
    setOptions(chartData) {
      this.chart.setOption({
        grid: {
          /*  height: 94,
          width: 760, */
          bottom: 20,
          top: 55,
          right: 15,
          left: 59,
          show: true,
          borderColor: "#dfdfe1",
          borderWidth: 1
        },

        legend: {
          show: true,
          orient: "horizontal",
          right: 50,
          width: 600,
          top: 15,
          itemWidth: 15,
          itemGap: 20,
          data: ["优化调度", "水量"]
        },
        tooltip: {
          trigger: "axis",
          padding: [0, 10],
          confine: true,
          /*  formatter: function(params) {
           
          }, */
          textStyle: {
            align: "left"
          }
        },
        xAxis: [
          {
            data: chartData.time,
            axisLabel: {
              inside: false,
              textStyle: {
                color: "#fff"
              }
            },
            axisTick: {
              show: false
            },
            axisLine: {
              show: false
            },
            z: 10
          }
        ],
        yAxis: {
          /*  splitNumber: 6, */
          /*  min:0.09, */
          splitLine: {
            show: false,
            lineStyle: {
              color: "#dfdfdf",
              width: 1,
              type: "dashed"
            }
          },
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            textStyle: {
              color: "#999"
            }
          }
        },
        series: [
          {
            name: "优化调度",
            type: "bar",
            barWidth : 2,
            data: chartData.warningdata,
            zlevel: 9,
            itemStyle: {
              normal: {
                color: "#b53f9a"
              }
            }
          },

          {
            name: "水量",
            type: "line",
            symbol: "none",
            data: chartData.waterdata,
            zlevel: 1,
            itemStyle: {
              normal: {
                borderColor: "#99b9ea",
                color: "#99b9ea"
              }
            },
            lineStyle: {
              normal: {
                width: 0,
                color: "#99b9ea"
              }
            },
            areaStyle: {
              normal: {
                color: "#99b9ea"
              }
            }
          }
        ]
      });
    },
    initChart() {
      this.chart = this.$echarts.init(this.$el, "macarons");
      this.chart.clear();
      this.setOptions(this.chartData);

      this.chart.on("legendselectchanged", function(params) {
        console.log("params", "color:red", params.selected);
        /* this.objlegend=params.selected */
        sessionStorage.setItem("objlegend", JSON.stringify(params.selected));
      });
    }
  }
};
</script>





