<template>
  <div class="outbox">
    <div
      v-for="(item,index) in list"
      :key="index"
      :style="{height:(314/list.length)+'px'}"
      class="itembox"
    >
      <div :id="'pmyDiv1'+index" :style="{height:(314/list.length)+'px',width:width}"></div>
      <!--  <div class="bengnane" :style="{height:(314/list.length)+'px'}">{{index+1}}泵</div> -->
    </div>
  </div>
</template>
<script>
import { debounce } from "@/utils";
import Bus from "@/bus.js";
export default {
  name: "p1echarts",
  props: {
    /*  className: {
      type: String,
      default: "chart"
    }, */
    width: {
      type: String,
      default: "100%"
    },
    height: {
      type: String,
      default: "70px"
    },
    autoResize: {
      type: Boolean,
      default: true
    }
    /*  chartData: {
      type: Object,
      required: true
    } */
  },
  data() {
    return {
      list: [
       
      ],
      colorlist: [
        "#9AFF9A",
        "#97FFFF",
        "#9370DB",
        "#B452CD",
        "#BDB76B",
        "#BCEE68",
        "#836FFF",
        "#708090"
      ],
      pmyDivbox: [],
      list2: []
    };
  },

  mounted() {
    var _this = this;
    Bus.$on("toshuichangddcompare", e => {
      var OptimalMaxData = e.OptimalMaxData;
      var OptimalDispatchData = e.OptimalDispatchData;
      var PumpCurveData = e.OptimalDispatchData.PumpCurveData;

      var tlist = [];
      for (var j = 0; j < PumpCurveData.length; j++) {
        var prelist = [];
        for (var i = 0; i < PumpCurveData[j].PumpData.length; i++) {
          var dt = i < 0 ? "" : PumpCurveData[j].PumpData[i].Data;
          prelist.push(dt);
        }
        tlist.push(prelist);
      }
      _this.list = tlist;
     // console.log("%c_this.list", "font-size:22px;color:green", _this.list);

      var tlist2 = [];
      for (var j = 0; j < PumpCurveData.length; j++) {
        var prelist2 = [];
        for (var i = 0; i < PumpCurveData[j].PumpData.length; i++) {
          var dt2 = i < 0 ? PumpCurveData[j].PumpData[i].Data : "";
          prelist2.push(dt2);
        }
        tlist2.push(prelist2);
      }
      _this.list2 = tlist2;
      setTimeout(() => {
        _this.$nextTick(_this.drawLine());
      }, 200);
    });

    /*  _this.$nextTick(_this.drawLine()); */
  },
  beforeDestroy() {
    if (this.autoResize) {
      window.removeEventListener("resize", this.__resizeHandler);
    }
  },
  methods: {
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      var length = this.list.length;
      for (var i = 0; i < length; i++) {
        this.pmyDivbox[i] = "pmyDiv1" + i;
        /*  let myChart = this.$echarts.init(document.getElementById(this.pmyDivbox[i])); */
        let myChart = this.$echarts.init(
          document.getElementById(this.pmyDivbox[i])
        );
        /* echarts随浏览器窗口变化自适应开始 */
        if (this.autoResize) {
          this.__resizeHandler = debounce(() => {
            if (myChart) {
              myChart.resize();
            }
          }, 100);
          window.addEventListener("resize", this.__resizeHandler);
        }

        /*   let sizeFun = function() {
          myChart.resize();
        };
        window.addEventListener("resize", sizeFun);
 */
        /* echarts随浏览器窗口变化自适应结束 */
        // 绘制图表
        var option = {
          grid: {
            bottom: 40,
            top: 60,
            right: 85,
            left: 55,
            show: false,
            borderColor: "#dfdfe1",
            borderWidth: 1
          },
          /*  title: {
          text: "(MPa)",
          right: 0,
          top: 10,
          textStyle: {
            color: "#999",
            fontWeight: "normal",
            fontSize: 12
          }
        }, */
          xAxis: [
            {
              type: "category",
              data: this.list[i],
              axisLabel: {
                show: false
              },
              axisPointer: {
                type: "shadow"
              },
              axisTick: { show: false },
              axisLine: {
                show: true,
                lineStyle: {
                  color: "#b1b1b1",
                  width: 1
                }
              }
            }
          ],
          yAxis: [
            {
              type: "value",
              axisLabel: {
                show: false
              },
              axisTick: { show: false },
              splitLine: { show: false }, //去除网格线
              axisLine: {
                show: false
              }
            }
          ],
          series: [
            {
              /*  name: "表径", */
              type: "line",
              data: this.list[i],
              symbol: "none",
              areaStyle: {
                normal: {
                  color: this.colorlist[i]
                }
              },
              lineStyle: {
                normal: {
                  width: 1,
                  color: this.colorlist[i]
                }
              }

              /*  itemStyle: {
                normal: {
                  color: "#c3d2ea"
                }
              }, */
            },
            {
              /*  name: "表径", */
              type: "line",
              data: this.list2[i],
              areaStyle: {
                normal: {
                  color: "#000"
                }
              },
              lineStyle: {
                normal: {
                  width: 1,
                  color: "#000"
                }
              }

              /*  itemStyle: {
                normal: {
                  color: "#c3d2ea"
                }
              }, */
            }
          ]
        };

        myChart.setOption(option);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.outbox {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  width: calc(100vw - 40px - 12vw);
  height: 334px;
  /*   background-color: aqua; */
  /*  border-bottom: 1px solid aqua; */
  margin-top: 20px;
  .itembox {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    width: calc(100vw - 40px - 12vw);
    /*  border-top: 1px solid aqua; */
    /*        border-bottom: 1px solid aqua; */
    /* .bengnane {
      display: flex;
      flex-flow: row nowrap;
      justify-content: flex-start;
      align-items: center;
      width: 60px;
      background-color: blueviolet;
    } */
  }
}
</style>





