<template>
  <div class="outbox">
    <div class="outbox_title">
      <div class="outbox_title_leftoutbox">各水厂调度曲线对比</div>
      <div class="outbox_title_rightoutbox">
        <div class="outbox_title_rightoutbox_selectbox1">
          <div class="outbox_title_rightoutbox_selectbox1_left">时间:</div>
          <div class="outbox_title_rightoutbox_selectbox1_right">
            <el-date-picker
              v-model="form.birthdayName"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
              @change="selectTime"
              type="date"
              placeholder="选择日期"
            ></el-date-picker>
          </div>
        </div>
        <div class="outbox_title_rightoutbox_selectbox2">
          <div class="outbox_title_rightoutbox_selectbox2_left">水厂:</div>
          <div class="outbox_title_rightoutbox_selectbox2_right">
            <el-select v-model="value" placeholder="请选择" @change="currentSelPresure">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
        </div>
      </div>
    </div>
    <div class="outbox_kdc">
      <div class="outbox_kdc_inbox">
        <div class="outbox_kdc_inbox_num">
          <div
            v-for="(item1,index1) in 24"
            :key="index1"
            class="item1_box"
            :style="{width:(1586/24)+'px'}"
          >{{item1-1==24?"":item1-1}}</div>
        </div>
        <div class="outbox_kdc_inbox_line">
          <div
            v-for="(item2,index2) in 96"
            :key="index2"
            class="item2_box"
            :class="(index2%4)==0?'longlinestyle':''"
          ></div>
        </div>
      </div>
    </div>
    <div class="outbox_echartsbox1">
      <div class="outbox_echartsbox1_left">
        <div class="outbox_echartsbox1_left_top">{{value}}</div>
        <div class="outbox_echartsbox1_left_bottom">优化调度曲线</div>
      </div>
      <div class="outbox_echartsbox1_right">
        <yhddechartsbox/>
      </div>
    </div>
    <div class="outbox_echartsbox2">
      <div class="outbox_echartsbox2_left">
        <div class="outbox_echartsbox2_left_top">{{value}}</div>
        <div class="outbox_echartsbox2_left_bottom">实际调度曲线</div>
      </div>
      <div class="outbox_echartsbox2_right">
       <realddechartsbox/>
      </div>
    </div>
  </div>
</template>
<script>
import Bus from "@/bus.js";
import urlClass from "@/components/js/UrlClass";
import yhddechartsbox from '@/components/rcddcompare/shuichangddcompare/yhddechartsbox/yhddechartsbox'
import realddechartsbox from '@/components/rcddcompare/shuichangddcompare/realddechartsbox/realddechartsbox'
export default {
  name: "index",
  components: {
    yhddechartsbox,
    realddechartsbox
  },
  data() {
    return {
      options: [
       /*  {
          value: "笔架山水厂",
          label: "笔架山水厂"
        },
        {
          value: "大涌北水厂",
          label: "大涌北水厂"
        },
        {
          value: "大涌南水厂",
          label: "大涌南水厂"
        },
        {
          value: "东湖水厂",
          label: "东湖水厂"
        },
        {
          value: "梅林水厂",
          label: "梅林水厂"
        },
        {
          value: "南山水厂",
          label: "南山水厂"
        } */
      ],
      value: "",
      form: {
        birthdayName: ""
      }
    };
  },
  mounted() {
    var date = new Date();
    this.form.birthdayName = this.$moment()
      .subtract(1, "days")
      .format("YYYY-MM-DD");
      this.GetWaterworksName() 
  },
  methods: {
    /* 获取select选择框水厂名称数据 */
    GetWaterworksName() {
      var _this = this;
       _this.$axios
        .post(
          urlClass.axiosUrlRC + "GetWaterworksName"
         //"https://www.easy-mock.com/mock/5d4d32f071ea7e33836a0ed0/dddbWaterworksName/dddbWaterworksName"
        )
        .then(res => {
          var selectarray=[]
          for(var i=0;i<res.data.WaterworksName.length;i++){
            var value=res.data.WaterworksName[i]
            var label=res.data.WaterworksName[i]
            var obj={value,label}
            selectarray.push(obj)
          }
          this.options=selectarray
          this.value=res.data.WaterworksName[0]
         /*  console.log("获取select选择框水厂名称数据 GetWaterworksName", res);
          console.log("this.options",this.options); */
             this.WaterworksDispatchCurveData();
        })
        .catch(error => {});
    },
    /* 1.日常选择表 */
    selectTime(val) {
      this.form.birthdayName = val;
      //console.log(val);
      this.WaterworksDispatchCurveData();
    },
    /* 2.select选择框 */
    currentSelPresure(Val) {
      this.value = Val;
      this.WaterworksDispatchCurveData();
    },
    /* 3.	各水厂调度曲线对比 WaterworksDispatchCurveData*/
    WaterworksDispatchCurveData() {
      var _this = this;
      var Date = _this.form.birthdayName;
      var WaterNum = _this.value;
     // console.log("3.	各水厂调度曲线对比 参数", { Date, WaterNum });
       _this.$axios
        .post(
          urlClass.axiosUrlRC + "WaterworksDispatchCurveData",
          //"https://www.easy-mock.com/mock/5d510ac4f885aa5003118e28/WaterworksDispatchCurveData/WaterworksDispatchCurveData",
          JSON.stringify({ Date,WaterNum }),
          {
            headers: { "Content-Type": "application/json;" }
          }
        )
        .then(res => {
          //console.log("3.	获取调度对比曲线 WaterworksDispatchCurveData ", res);
          Bus.$emit("toshuichangddcompare",res.data)
        })
        .catch(error => {});
    }
  }
};
</script>
