<template>
  <div class="outboxt" ref="orderBox">
    <vue-scroll>
      <div class="inbox">
        <div class="hedertitlebox">
          <div class="hedertitlebox_left">调度对比</div>
          <div class="hedertitlebox_right" @click="combackrcdd">返回</div>
        </div>
        <div class="contentbox">
          <div class="ddechartsbox">
            <ddecharts />
          </div>
          <div class="shuichangddcompare">
            <shuichangddcompare />
          </div>
          <div class="ddfnbox">
            <!-- 暂无数据 -->
            <div class="zwsj" v-if="!ddtablelelemgth">暂无数据</div>
            <!-- 暂无数据结束 -->
            <ddfn />
          </div>
        </div>
        <!-- 定位返回顶部按钮 -->
        <div class="go-top" v-show="btnFlag" @click="backTop"></div>
        <!-- 定位返回顶部按钮结束 -->
      </div>
    </vue-scroll>
  </div>
</template>
<script>
import { Loading } from "element-ui";
import Bus from "@/bus.js";
import ddecharts from "./ddecharts/index";
import ddfn from "./ddfn/index";
import shuichangddcompare from "./shuichangddcompare/index";
export default {
  name: "index",
  components: {
    ddecharts,
    ddfn,
    shuichangddcompare
  },
  data() {
    return {
      btnFlag: false,
      ddtablelelemgth: 0
    };
  },
  mounted() {
    var _this = this;
    Bus.$on("torcddcompareindex",e=>{
      _this.ddtablelelemgth=e
    })
    // 先给页面注册滚动事件
   /*  console.log(_this.$refs.orderBox); */
    // 通过$refs获取dom元素
    var box = this.$refs.orderBox.getElementsByClassName("__panel")[0];
    // 监听这个dom的scroll事件
    box.addEventListener(
      "scroll",
      () => {
        /*  var isScroll=_this.$refs.orderBox.scrollTop>230 */
        var isScroll = box.scrollTop;
        if (isScroll > 230) {
          _this.btnFlag = true;
        } else {
          _this.btnFlag = false;
        }
       /*  console.log("%cisScroll", "color:green", isScroll);
        console.log("scroll " + box.scrollTop); */
      },
      false
    );
  },
 /*  destroyed() {
    window.clearInterval();
    
  }, */
  methods: {
    combackrcdd() {
      this.$router.go(-1);
    },
   
    // 点击图片回到顶部方法，加计时器是为了过渡顺滑
    backTop() {
      let that = this;
      let timer = setInterval(() => {
        let ispeed = Math.floor(
          -that.$refs.orderBox.getElementsByClassName("__panel")[0].scrollTop /
            5
        );
        that.$refs.orderBox.getElementsByClassName("__panel")[0].scrollTop =
          that.$refs.orderBox.getElementsByClassName("__panel")[0].scrollTop +
          ispeed;
        if (
          that.$refs.orderBox.getElementsByClassName("__panel")[0].scrollTop ===
          0
        ) {
          clearInterval(timer);
        }
      }, 16);
    }
  }
};
</script>
<style scoped>
.outboxt {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
}
.inbox {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
  width: 100vw;

  /* background-color: aquamarine; */
}
.hedertitlebox {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  height: 56px;
}
.hedertitlebox_left {
  width: 80px;
  height: 32px;
  font: bold 18px/32px "微软雅黑";
  color: #8b95a2;
  margin-left: 20px;
}
.hedertitlebox_right {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  width: 56px;
  height: 32px;
  font: normal 12px/32px "微软雅黑";
  color: #6e7b8b;
  margin-right: 20px;
  position: relative;
  cursor: pointer;
}
.hedertitlebox_right::before {
  content: url("~@/assets/img/back_normal.png");
  /*     position: absolute; */
}
.hedertitlebox_right:hover {
  color: #70991f;
}
.hedertitlebox_right:hover::before {
  content: url("~@/assets/img/back_hover.png");
  /*     position: absolute; */
}
.contentbox {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
  width: calc(100vw - 40px);
  border-left: 1px #d1d1da solid;
  border-right: 1px #d1d1da solid;

  /*   height: 60px;
  background-color: #70991f */
}
.ddechartsbox {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  width: calc(100vw - 40px);
  /* height: 600px; */
  height: 595px;
  /*    background-color: aquamarine; */
}
.shuichangddcompare {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  width: calc(100vw - 40px);
  height: 856px;
  /* background-color: aquamarine; */
}
.ddfnbox {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  width: calc(100vw - 40px);
  position: relative;
}
.zwsj {
  position: absolute;
  width: calc(100vw - 40px);
  height: 200px;
  font: normal 12px/280px "微软雅黑";
  color: #6e7b8b;
  text-align: center;
  top: 60px;
  z-index: 0;
  /* background-color: aquamarine; */
}
/*  定位返回顶部按钮 */
.go-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  height: 60px;
  width: 60px;
  background-image: url("../../assets/img/top_normal.png");
  background-repeat: no-repeat;
  cursor: pointer;
}
.go-top:hover {
  background-image: url("../../assets/img/top_hover.png");
  background-repeat: no-repeat;
  cursor: pointer;
}
</style>





